<template>
  <div class="order-detail__wrapper">
    <div class="header">
      <div class="header__back iconfont" @click="handleBackClick">&#xe6f2;</div>
      <div class="header__title">订单详情</div>
    </div>
    <div v-if="order.id" class="content">
      <!-- 商品列表 -->
      <div class="info-card">
        <div class="info-card__title">{{ order.shopName }}</div>
        <div class="product-list">
          <div
            class="product"
            v-for="item in order.products"
            :key="item.product.id"
          >
            <img class="product__img" :src="item.product.imgUrl" />
            <div class="product__info">
              <p class="product__name">{{ item.product.name }}</p>
              <p class="product__spec">
                ¥{{ item.product.price }} x {{ item.num }}
              </p>
            </div>
            <div class="product__price">
              ¥{{ (item.product.price * item.num).toFixed(2) }}
            </div>
          </div>
        </div>
        <div class="cost-summary">
          <div class="cost-summary__item">
            <span>商品总价</span>
            <span>¥{{ order.totalPrice }}</span>
          </div>
        </div>
        <div class="total-price">
          <span>实付</span>
          <span class="total-price__amount">¥{{ order.totalPrice }}</span>
        </div>
      </div>

      <!-- 订单信息 -->
      <div class="info-card">
        <div class="info-card__title">订单信息</div>
        <div class="info-card__item">
          <div class="info-card__item__label">订单号:</div>
          <div class="info-card__item__value">{{ order.id }}</div>
        </div>
        <div class="info-card__item">
          <div class="info-card__item__label">下单时间:</div>
          <div class="info-card__item__value">
            {{ order.updatedAt }}
          </div>
        </div>
      </div>
    </div>
    <div v-else class="loading">正在加载订单...</div>
  </div>
</template>
<script>
import { ref } from "vue";
import { useRoute, useRouter } from "vue-router";
import { get } from "../../utils/request";

// 处理订单详情逻辑
const useOrderDetailEffect = () => {
  const route = useRoute();
  const order = ref({});

  const getOrderDetail = async () => {
    const result = await get(`/api/order/${route.params.id}`);
    if (result?.errno === 0 && result?.data) {
      order.value = result.data;
      let totalPrice = 0;
      let totalNumber = 0;
      order.value.products?.forEach(productItem => {
        totalNumber += productItem?.num || 0;
        totalPrice +=
          (productItem?.product?.price || 0) * (productItem?.num || 0);
      });
      order.value.totalPrice = totalPrice.toFixed(2);
      order.value.totalNumber = totalNumber;
    }
  };
  getOrderDetail();
  return order;
};

// 处理返回逻辑
const useBackRouterEffect = () => {
  const router = useRouter();
  const handleBackClick = () => {
    router.back();
  };
  return { handleBackClick };
};

export default {
  name: "OrderDetail",
  setup() {
    const order = useOrderDetailEffect();
    const { handleBackClick } = useBackRouterEffect();
    return { order, handleBackClick };
  }
};
</script>
<style lang="scss" scoped>
$dark-bgColor: #f8f8f8;
$content-fontcolor: #333;
$bgColor: #fff;
$light-fontColor: #999;
$hightlight-fontColor: #e93b3b;
$content-bgColor: #f1f1f1;

@mixin ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.order-detail__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $dark-bgColor;
  color: $content-fontcolor;
  display: flex;
  flex-direction: column;
  font-size: 0.14rem;
}

.header {
  position: relative;
  flex-shrink: 0;
  line-height: 0.44rem;
  background: $bgColor;
  text-align: center;
  font-size: 0.16rem;
  color: $content-fontcolor;
  border-bottom: 0.01rem solid $content-bgColor;

  &__back {
    position: absolute;
    left: 0.18rem;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    font-size: 0.22rem;
    color: #333;
    cursor: pointer;
  }
}

.content {
  flex-grow: 1;
  overflow-y: auto;
  padding: 0.16rem 0.18rem;
}

.loading {
  padding-top: 0.5rem;
  text-align: center;
  color: $light-fontColor;
}

.status-card {
  background: linear-gradient(135deg, #ff9800, #f57c00);
  border-radius: 0.1rem;
  padding: 0.2rem;
  text-align: center;
  color: #fff;
  margin-bottom: 0.16rem;
  box-shadow: 0 0.04rem 0.12rem rgba(0, 0, 0, 0.1);

  &__title {
    margin: 0;
    font-size: 0.22rem;
    font-weight: 600;
  }
  &__desc {
    margin: 0.08rem 0 0;
    font-size: 0.14rem;
    opacity: 0.9;
  }
}

.info-card {
  background: $bgColor;
  border-radius: 0.08rem;
  padding: 0.16rem;
  margin-bottom: 0.16rem;
  box-shadow: 0 0.02rem 0.08rem rgba(0, 0, 0, 0.05);

  &__title {
    font-size: 0.16rem;
    font-weight: 600;
    margin: 0 0 0.16rem 0;
    padding-bottom: 0.1rem;
    border-bottom: 0.01rem solid $content-bgColor;
  }

  &__item {
    display: flex;
    line-height: 0.24rem;
    margin-bottom: 0.08rem;

    &:last-child {
      margin-bottom: 0;
    }

    &__label {
      color: $light-fontColor;
      width: 0.7rem;
      flex-shrink: 0;
    }
    &__value {
      flex-grow: 1;
    }
  }
}

.product-list {
  .product {
    display: flex;
    align-items: center;
    padding: 0.08rem 0;

    &:not(:last-child) {
      border-bottom: 0.01rem solid $content-bgColor;
    }

    &__img {
      width: 0.4rem;
      height: 0.4rem;
      border-radius: 0.04rem;
      margin-right: 0.12rem;
    }

    &__info {
      flex-grow: 1;
      min-width: 0; // Fix for flexbox overflow
    }

    &__name {
      margin: 0;
      font-size: 0.14rem;
      line-height: 0.2rem;
      font-weight: 500;
      @include ellipsis;
    }

    &__spec {
      margin: 0.02rem 0 0;
      font-size: 0.12rem;
      color: $light-fontColor;
    }

    &__price {
      font-size: 0.14rem;
      font-weight: 500;
      color: $content-fontcolor;
      margin-left: 0.1rem;
    }
  }
}

.cost-summary {
  padding: 0.16rem 0;
  margin-top: 0.16rem;
  border-top: 0.01rem solid $content-bgColor;

  &__item {
    display: flex;
    justify-content: space-between;
    line-height: 0.26rem;

    span:first-child {
      color: $light-fontColor;
    }
    span:last-child {
      color: $content-fontcolor;
    }

    .discount {
      color: $hightlight-fontColor;
    }
  }
}

.total-price {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  text-align: right;
  margin-top: 0.08rem;
  color: $content-fontcolor;

  &__amount {
    font-size: 0.2rem;
    font-weight: 600;
    color: $hightlight-fontColor;
    margin-left: 0.08rem;
  }
}
</style>
